.page-header {
  position: relative;
  margin-bottom: 56px;
}

.page-header__logo img {
  position: absolute;
  bottom: -82px;
  left: 109px;
  z-index: 2;
  width: 102px;
  height: 83px;
  background-color: transparent;
}

@media (min-width: @tablet-width) {
  .page-header {
    margin-bottom: 0;
  }

  .page-header__logo img {
    top: 0;
    left: 333px;
  }
}

@media (min-width: @desktop-width) {
  .page-header__logo img {
    left: 505px;
    width: 188px;
    height: 100px;
  }
}
