  width: 260px;
  margin-bottom: 25px;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}
.advantages__intro {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 280px;
  margin: 0 auto;
  margin-bottom: 5px;
  line-height: 26px;
  color: #333333;
}
.advantages__group {
  background-color: #f2f2f2;
}
