}
.advantages-item--small-square .advantages-item__description,
.advantages-item--beautiful-road .advantages-item__description,
.advantages-item--little-tourists .advantages-item__description {
  color: #333333;
}
@media (max-width: 767px) {
  .advantages-item__title br,
  .advantages-item__wrapper--diavols-bridge br {
    display: none;
  }
  .advantages-item__wrapper--real-city::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 20;
    width: 320px;
    height: 37px;
    background-image: url("../img/bg-triangle.svg");
  }
  .advantages-item--small-square .advantages-item__description,
  .advantages-item--beautiful-road .advantages-item__description {
    padding-bottom: 30px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
  .advantages-item--little-tourists .advantages-item__description {
    padding-bottom: 30px;
  }
}
@media (min-width: 768px) {
  .advantages-item__wrapper {
    width: 256px;
  }
  .advantages-item__title {
    font-size: 21px;
  }
  .advantages-item__number {
    padding-bottom: 22px;
  }
  .advantages-item__description br {
    display: none;
  }
  .advantages-item--real-city,
  .advantages-item--diavols-bridge {
    display: flex;
    flex-direction: row;
    width: 768px;
