  .advantages-item__wrapper--diavols-bridge {
    width: 162px;
    padding-top: 44px;
    padding-right: 47px;
    padding-bottom: 20px;
    padding-left: 47px;
  }
  .advantages-item__wrapper--real-city .advantages-item__description {
    width: 170px;
    padding-left: 0;
  }
  .advantages-item__wrapper--diavols-bridge img {
    order: 1;
  }
  .advantages-item__wrapper--diavols-bridge {
    order: 2;
  }
  .advantages-item--small-square {
    width: 160px;
    margin-left: 50px;
  }
  .advantages-item--beautiful-road {
    width: 140px;
    margin-left: 18px;
  }
  .advantages-item--little-tourists {
    width: 150px;
    margin-left: 25px;
  }
}
@media (min-width: 1200px) {
  .advantages-item__image {
    width: 800px;
    height: 256px;
  }
  .advantages-item--real-city,
  .advantages-item--diavols-bridge {
    width: 1200px;
  }
  .advantages-item__wrapper--real-city .advantages-item__description {
    width: 270px;
