.buttons {
  text-align: center;
  color: #ffffff;
  background-color: #81b3d2;
  border: none;
}
.buttons:hover,
.buttons:focus {
  background-color: #669ec0;
}
.buttons:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.buttons:disabled {
  background-color: #e5e5e5;
}
.comment,
.comment__title {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
}
.comment {
  width: 320px;
  padding-top: 40px;
  text-align: center;
}
.comment__title {
  margin-bottom: 27px;
}
.comment__description {
  margin-bottom: 0;
  padding-bottom: 14px;
  font-size: 14px;
  font-weight: normal;
  line-height: 24px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.comment__form {
  padding-top: 20px;
}
.comment__form fieldset {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-bottom: 18px;
  padding-bottom: 5px;
  text-align: left;
  border: 0 none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.comment__item legend {
  margin: 0 auto;
  margin-bottom: 20px;
  padding-left: 0;
}
.comment__item ul,
.comment__item li {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-left: 10px;
}
.comment__label {
  font-size: 14px;
  font-weight: normal;
  color: #333333;
}
.comment__label label {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-right: 10px;
}
.comment__label input,
.comment__textarea {
  padding: 5px;
  font-size: 18px;
  font-weight: bold;
  color: #000000;
  background-color: #f2f2f2;
  border: 4px solid #f2f2f2;
}
.comment__label input:hover,
.comment__textarea:hover {
  background-color: #e5e5e5;
  border: 4px solid #e5e5e5;
  outline: none;
}
.comment__label input:active,
.comment__textarea:active,
.comment__label input:focus,
.comment__textarea:focus {
  height: 50px;
  background-color: #ffffff;
  border: 4px solid #e5e5e5;
  outline: none;
}
.comment__label input {
  margin: 0;
  padding: 0;
  height: 50px;
  padding-left: 20px;
  list-style: none;
}
.comment__label--radio,
.comment__label--checkbox {
  font-weight: bold;
  color: #000000;
  text-transform: none;
}
.comment__item--name li {
  margin-bottom: 20px;
}
.comment__label--name input {
  width: 208px;
}
.comment__label--surname input {
  width: 170px;
}
.comment__label--patronymic input {
  width: 176px;
}
.comment__item--contacts li {
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
}
.comment__item--contacts label {
  position: relative;
  padding-left: 20px;
}
.comment__item--contacts input {
  width: 222px;
  padding-left: 20px;
}
.comment__label--phone input {
  margin-top: 5px;
}
.comment__label--email input {
  margin-top: 7px;
  margin-bottom: 6px;
}
.comment__label--phone label::before {
  content: "";
  position: absolute;
  top: 31px;
  left: 222px;
  z-index: 20;
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
}
.comment__label--phone label::after {
  content: "";
  position: absolute;
  top: 47px;
  left: 242px;
  z-index: 20;
  width: 18px;
  height: 18px;
  background-image: url("../img/icon-phone.svg");
}
.comment__label--email label::before {
  content: "";
  position: absolute;
  top: 33px;
  left: 221px;
  z-index: 20;
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
}
.comment__label--email label::after {
  content: "";
  position: absolute;
  top: 50px;
  left: 242px;
  z-index: 20;
  width: 18px;
  height: 18px;
  background-image: url("../img/icon-mail.svg");
}
.comment__item--impression legend {
  margin-bottom: 40px;
}
.comment__item--sightseeing legend {
  margin-bottom: 30px;
  text-align: center;
}
.comment__item--impression li,
.comment__item--sightseeing li {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
  margin-left: 80px;
}
.comment__item--impression li:last-child {
  margin-bottom: 36px;
}
.comment__item--sightseeing li {
  margin-bottom: 54px;
  margin-left: 80px;
  text-align: left;
}
.comment__item--sightseeing li:last-of-type {
  margin-bottom: 32px;
}
.comment__item--impression label,
.comment__item--sightseeing label {
  font-size: 18px;
}
.comment__label--radio label::before {
  content: "";
  position: absolute;
  top: -16px;
  left: -70px;
  width: 55px;
  height: 55px;
  background-color: #f2f2f2;
  border-radius: 50%;
}
.comment__label--radio:hover label::before {
  background-color: #e5e5e5;
}
.comment__label--radio:active label::before,
.comment__label--radio:focus label::before {
  background-color: #d5d5d5;
}
.comment__radio:checked + label::after {
  content: "";
  position: absolute;
  top: 1px;
  left: -52px;
  width: 20px;
  height: 20px;
  background-color: #333333;
  border-radius: 50%;
}
.comment__radio:checked:active + label::after,
.comment__radio:checked:focus + label::after {
  background-color: #333333;
  opacity: 0.3;
}
.comment__check-box + label::before {
  content: "";
  position: absolute;
  top: -12px;
  left: -70px;
  width: 55px;
  height: 55px;
  background-color: #f2f2f2;
}
.comment__check-box:hover + label::before {
